html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #fff;
  color: #000;
  font-family: "Union-Regular", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 1rem;
}

header {
  display: flex;
  flex-direction: row;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 1;
  align-items: center;
  width: 100%;
  height: 3rem;
  background-color: #FFF;
  color: #000;
  line-height: 2.5rem;
}
header .header-left-pannel {
  display: flex;
  flex-direction: row;
  font-size: 30px;
  width: 66.66%;
  color: #d9dadb;
  background: #fff;
  position: relative;
}
header .header-left-pannel .hover {
  color: green;
  background-color: red;
}
header .header-left-pannel .logo {
  display: inline-block;
  width: 16.66%;
  height: 3rem;
  padding: 0 0.75rem;
  line-height: 2.75rem;
  text-decoration: none;
  color: #000;
}
header .header-left-pannel .transparent-button {
  background-color: transparent;
  border: none;
  border-radius: 0;
  box-shadow: none;
}
header .header-left-pannel .close-panel-button {
  right: calc(33.33vw + 1rem);
  position: fixed;
  pointer-events: auto;
  z-index: 2;
  top: 0;
  width: 3rem;
  height: 3rem;
  transition: transform 0.25s ease, -webkit-transform 0.25s ease;
}
header .header-left-pannel .button-cross {
  font-size: 2rem;
}
header .header-left-pannel .header-pannel, header .header-left-pannel .expanded {
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  padding-bottom: 140px;
  background-color: #E0E1E2;
  color: #000;
  pointer-events: auto;
  cursor: auto;
}
header .header-left-pannel .header-pannel section, header .header-left-pannel .expanded section {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  border-bottom: 1px solid #000;
  background-color: #E0E1E2;
  padding-bottom: 1rem;
}
header .header-left-pannel .header-pannel section:nth-child(5), header .header-left-pannel .expanded section:nth-child(5) {
  border-bottom: none;
}
header .header-left-pannel .header-pannel section .p-title, header .header-left-pannel .expanded section .p-title {
  display: inline-block;
  width: 11.66%;
  padding: 0 0.75rem;
}
header .header-left-pannel .header-pannel section .pannel-section, header .header-left-pannel .expanded section .pannel-section {
  width: 66.66%;
  padding: 0.75rem;
  font-size: 14px;
  line-height: 1.1rem;
}
header .header-left-pannel .header-pannel section p, header .header-left-pannel .header-pannel section ul, header .header-left-pannel .expanded section p, header .header-left-pannel .expanded section ul {
  margin-bottom: 1rem;
}
header .header-left-pannel .menu-about {
  display: block;
  flex-direction: row;
  width: 100%;
  height: 3rem;
  line-height: 2.75rem;
}
header .header-left-pannel span {
  display: inline-block;
  margin-right: 3rem;
}
header .header-left-pannel span:first-child {
  padding-left: 0.75rem;
}
header .header-right-pannel {
  position: relative;
  display: flex;
  align-items: center;
  width: 33.333%;
  height: 3rem;
  font-size: 30px;
  cursor: pointer;
}
header .header-right-pannel:hover {
  color: #000;
  background-color: #E0E1E2;
}
header .header-right-pannel .menu-right {
  text-decoration: none;
  color: #000;
}
header .header-right-pannel .menu-right:nth-child(1) {
  width: 33.33%;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
}
header .header-right-pannel .menu-right:nth-child(2) {
  width: 66.66%;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
}
header .header-right-pannel .last-menu {
  position: absolute;
  z-index: 1;
  top: 2px;
  right: 0;
  padding: 0 1rem;
}
header .header-right-pannel .last-menu .last-item {
  text-decoration: none;
  color: #000;
  font-size: 14px;
}

section {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(7, 1fr);
  background-color: #fff;
}
section img {
  vertical-align: middle;
  width: 100%;
  transition: opacity 0.25s ease;
  background-color: #FFF;
}
section .title {
  display: inline-flex;
  align-items: center;
  width: 100%;
  height: 2rem;
  line-height: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 0.75rem;
  text-transform: uppercase;
  text-decoration: none;
  color: #000;
}